import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import { confirmation } from '../../actions/api/auth';
import { notifySuccess } from '../../actions/notification';

class MailerAuth extends Component {
  componentWillMount() {
    const { confirmationparams } = this.props;
    const params = [];
    confirmationparams.replace('?', '').split('&').forEach((x) => {
      const param = x.split('=')[1];
      params.push(param);
    });
    this.props.confirmation(params[1])
      .then((response) => {
        if (response.ok) {
          this.props.notifySuccess('Registration was successful');
        }
      });
  }

  render() {
    return (
      <Redirect to="/" />
    );
  }
}

MailerAuth.propTypes = {
  confirmationparams: PropTypes.any.isRequired,
  confirmation: PropTypes.func.isRequired,
  notifySuccess: PropTypes.func.isRequired,
};

function mapStateToProps(state, ownProps) {
  return {
    confirmationparams: ownProps.location.search,
  };
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    confirmation,
    notifySuccess,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(MailerAuth);
